.message {
    width: calc(100% - 32px);
    height: 250px;
    align-self: stretch;
    display: flex;
    resize: none;
    margin-top: 12px;
    padding: 9px 10px;
    border-radius: 4px;
    border: solid 0.5px var(--black-15);
    background-color: var(--light-gray-100);
}

.btnSubmit {
    width: 85%;
    height: 19px;
    bottom: 24px;
    position: fixed;
    left:50%;
    z-index: 2;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    padding: 15px;
    border-radius: 4px;
    background-color: var(--orange-100);
    color: var(--white-100);
}

.check-position{
    width: 17px;
    height: 17px;
    margin: 0;
    margin-left: 15px;
    margin-right: 5px;
    padding-top: 2px;
    justify-content: center;
  }

.text-address{
  height: 42px;
  width: 93%;
  margin-left: 2%;

  align-self: stretch;
  flex-grow: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 6px;
  padding: 9px 10px;
  opacity: 0.5;
  border-radius: 4px;
  border: solid 1px rgba(34, 34, 34, 0.15);
  background-color: var(--white-100);
  /* background-image: url('../src/images/arrow.png'); */
}

input::-webkit-input-placeholder{
    /* 올바른 이미지를 넣어야함 */
    background-image: url('data:image/png;base64,') ;
    background-size: contain;
    background-position:  80%;
    background-repeat: no-repeat;
    text-align: left;
    text-indent: 0;
  }